import * as RegisterApi from '../../api/register';

export const REQUEST_REGISTER_PAGE = 'REQUEST_REGISTER_PAGE';
export const openRegisterPageActionCreator = () => ({
  type: REQUEST_REGISTER_PAGE,
  payload: {
    isRegistering: true
  }
});

export const SET_USERNAME_INPUT = 'SET_USERNAME_INPUT';
export const setUsernameActionCreator = username => ({
  type: SET_USERNAME_INPUT,
  payload: {
    username: username || ''
  }
});

export const SET_PASSWORD_INPUT = 'SET_PASSWORD_INPUT';
export const setPasswordActionCreator = password => ({
  type: SET_PASSWORD_INPUT,
  payload: {
    password: password || ''
  }
});

export const SET_PASSWORD_VISIBILITY = 'SET_PASSWORD_VISIBILITY';
export const setPasswordVisibilityActionCreator = isPasswordVisible => ({
  type: SET_PASSWORD_VISIBILITY,
  payload: {
    isPasswordVisible
  }
});

export const USER_REGISTERED_SUCC = 'USER_REGISTERED_SUCC';
export const loginUser = user => ({
  type: USER_REGISTERED_SUCC,
  payload: {
    user,
    isRegistering: false
  }
});

export const HANDLE_REGISTER = 'HANDLE_REGISTER';
export const handleRegisterActionCreator = user =>
  dispatch =>
    RegisterApi.registerNewUser(user)
      .then(dispatch(loginUser(user)))
      .catch(error => console.log(error));


